import React from 'react'

class Navigation extends React.Component{

    constructor(props){
        super(props)
        this.state={
            flechaTB:'ion-arrow-down-b'
        }
    }

    handleClick=()=>{
        let flechaTB
        this.state.flechaTB==="ion-arrow-down-b"?flechaTB="ion-arrow-up-b":flechaTB="ion-arrow-down-b"
            this.setState({
                flechaTB
            })
    }
    

    render(){
        return(
            <header>
                <nav id="mainNav" className="navbar navbar-b navbar-expand-md fixed-top" role="navigation">
                    <div className = "container"> 
                        <a className="navbar-brand logo" href="#page-top"><img src="images/Asset 1.svg" width="200px" height="30px" alt="KosmoDev"/></a>
                        <button onClick={this.handleClick} className={"navbar-toggler collapsed " + this.state.flechaTB} type="button" data-toggle="collapse" data-target="#navbarDefault"aria-controls="navbarDefault"aria-expanded="false"aria-label="Toggle navigation">
                                        
                        </button>
                        <div className="navbar-collapse collapse justify-content-end"id="navbarDefault">
                            <ul className="navbar-nav">
                                <li className="navbar-item d-flex justify-content-center"><a className="nav-link js-scroll" href="index.html">Inicio</a></li>
                                <li className="navbar-item d-flex justify-content-center"><a className="nav-link js-scroll" href="#acerca">Acerca</a></li>
                                <li className="navbar-item d-flex justify-content-center"><a className="nav-link js-scroll" href="#servicios">Servicios</a></li>
                                <li className="navbar-item d-flex justify-content-center"><a className="nav-link js-scroll" href="#proyectos">Proyectos</a></li>
                                <li className="navbar-item d-flex justify-content-center"><a className="nav-link js-scroll" href="#blog">Blog</a></li>
                                <li className="navbar-item d-flex justify-content-center"><a className="nav-link js-scroll" href="#contact">Contact</a></li>
                            </ul>
                        </div>
                    </div>
                </nav>
            </header>
        )
    }
}

export default Navigation